import axios from 'axios';
import store from '../store';
import router from '../router';
import Vue from 'vue';
const Axios = axios.create({
  timeout: 20000,
  headers: {
    'X-Requested-With': 'XMLHttpRequest'
  }
});

// http request()
Axios.interceptors.request.use(
  (config: any) => {
    // baseURL
    if (process.env.NODE_ENV === 'production' && !process.env.VUE_APP_TEST) {
      config.baseURL = 'https://api-gateway.uoko.com/';
      // config.baseURL = 'http://bi.uoko.com:9999/app/webapi/';
    } else if (process.env.NODE_ENV === 'production' && process.env.VUE_APP_TEST) {
      config.baseURL = 'http://192.168.200.120:7070/'; // 
      // config.baseURL = 'https://api-gateway-pre.uoko.com'; // pre
    } else {
      config.baseURL = 'http://192.168.200.44:7070/'; // 
      // config.baseURL = 'http://172.16.3.103:8008/'; // 
      // config.baseURL = 'http://front-end.testuoko.com:3000/mock/22/'; // mock
    }
    /*,  Authorization */
    if (config.url.indexOf('/auth/asset/register_login/web/mobile') > -1
      || config.url.indexOf('/verification_code') > -1) {
      config.headers.Authorization = 'Basic b3duZXI6MTIzNDU2';
    } else {
      const token: string | null = localStorage.getItem('siteToken');
      config.headers.Authorization = token !== null ? `Bearer ${ token}` : '';
    }
    return config;
  }, (error) => {
    return Promise.reject(error);
  }
);

// http response
Axios.interceptors.response.use(
  (response: any) => {
    // 
    if (response.status === '401') {
      localStorage.removeItem('siteToken'); // token
      localStorage.removeItem('userId'); // userId
      router.push({
        path: '/bind'
      });
    }
    return response.data;
  }, (error) => {
    // const config: any = error.config;
    const status: any = error.response.status || 500;
    if (status === 401) {
      Vue.prototype.$toast({
        duration: 3000,       //  toast
        type: 'fail',
        message: ``
      });
      setTimeout(() => {
        router.push({
          path: '/bind'
        });
      }, 3000);
    } else {
      Vue.prototype.$toast({
        duration: 3000,       //  toast
        type: 'fail',
        message: ``
      });
    }
    // return Promise.reject(error);
  }
);

export default Axios;
